#app-footer {
  width: 100%;
  padding: 12px 0;
  color: #DFDFDF;
  text-align: center;
  background-color: #2F2F2F;
  text-shadow: 2px 2px 8px #0F0F0F
}

#app-footer h4 {
  margin: 8px
}

#div-logo-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center
}

.fa-logo {
  margin: 8px
}

#svg-sjb {
  margin: 4px
}

#svg-sjb circle,
#svg-sjb polygon {
  fill: #DFDFDF;
  -webkit-transition: fill 0.5s;
  -o-transition: fill 0.5s;
  transition: fill 0.5s
}

#svg-sjb:hover circle,
#svg-sjb:hover polygon {
  fill: goldenrod
}




